.wolves-btn {
  border: black 1px solid;
  background-color: var(--wolves-orange);
  height: 32px;
  width: 100%;
}

.wolves-btn:disabled {
  color: var(--wolves-orange);
  background-color: var(--wolves-blue-dk);
  opacity: 0.5;
}

.wolves-container {
  background-attachment: fixed;
  background-position-x: center;
  background-position-y: 0px;
  background-size: cover contain;
  background-repeat: auto repeat-y;
  position: relative;
  display: flex;
  flex-grow: 1;
  color: white;
  align-items: center;
  flex-direction: column;
  padding: 15px;
}

.wolves-container.bg-wolves {
  background-image: url(./assets/background_wolves.png);
}

.wolves-container.bg-bois {
  background-image: url(./assets/background_bois.jpg);
}

.wolves-container.bg-yourPack {
  background-image: url(./assets/background_bois.jpg);
}

.wolves-container h2 {
  font-size: 30px;
  font-weight: 500;
}

.wolves-container h3 {
  font-size: 14px;
}

.wolves-container .line-container {
  height: 8px;
  width: 85%;
  display: flex;
  align-items: center;
}

.line-container .line {
  height: 1px;
  width: calc(100% - 16px);
  background-color: var(--wolves-orange);
}

.line-container .dot {
  width: 8px;
  height: 8px;
  background-color: var(--wolves-orange);
  border-radius: 50%;
}

.wolves-border {
  border: var(--wolves-orange) 1px solid;
}
