#page4-section-header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 85%;
}

#page4-section-header .link {
  color: white;
  cursor: pointer;
}

#page4-section-header .link:hover {
  text-decoration: none !important;
  border: 0 !important;
  color: var(--wolves-orange) !important;
}

#page4-section-header .link:hover {
  border-bottom: white 1px solid;
}

#page4-content-container {
  margin-top: 15px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 85%;
}

#page4-content-image {
  display: flex;
  align-items: baseline;
  max-width: 500px;
  flex-basis: 50%;
}

#page4-content-image-inner {
  border: grey 1px solid;
  background-image: url(../../assets/loading_animated.gif);
  background-size: cover;
  width: 100%;
}

#page4-content-image-inner::before {
  content: '';
  float: left;
  height: 0px;
  padding-top: 100%;
}

#page4-content-image-inner::after {
  content: '';
  height: auto;
  padding-top: 0px;
  clear: both;
}

#page4-content-image-inner .card-visual {
  display: block;
  width: 100%;
  height: 100%;
}

#page4-content-text {
  display: flex;
  flex-basis: 48%;
  min-width: calc(98% - 512px);
  flex-direction: column;
  justify-content: space-between;
  text-align: left;
}

.rarity-box {
  list-style: none;
  padding: 0px;
  margin-top: 30px;
}
.rarity-box h2 {
  line-height: 1;
}
.content-margin {
  margin-top: 25px;
}

@media all and (max-width: 800px) {
  #page4-content-container {
    justify-content: center;
  }

  #page4-content-image {
    flex-basis: 100%;
  }

  #page4-content-text {
    margin-top: 16px;
    flex-basis: 100%;
  }
}
