.card-container {
  min-width: 300px;
  min-height: 380px;
  margin: 15px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: flex-start;
  flex-basis: 21%;
}

.card-container a {
  max-height: 300px;
}

.card-container .card-visual {
  width: 300px;
  height: 300px;
  margin-bottom: 4px;
  border: grey 1px solid;
  background-image: url(../../assets/loading_animated.gif);
  background-size: 300px 300px;
}

#triangle-up {
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 6px solid white;
  margin-top: 5px;
}

.line-h {
  line-height: 24px;
}
