.navbar-nav {
  letter-spacing: 0.02em;
  font-family: 'vincente', serif;
  font-weight: bold;
}

.navbar-collapse a,
.navbar-collapse span {
  letter-spacing: 0.02em;
  font-family: 'vincente', serif;
  font-weight: 500;
  font-size: 28px;
  line-height: 1em;
  color: white;
  margin: 0 15px;
}

.navbar-collapse a:hover {
  text-decoration: none;
  border-bottom: white 2px solid;
}

.navbar-collapse span {
  color: var(--wolves-orange);
}
.navbar-collapse {
  justify-content: center;
}

.logo {
  margin: 0px 10px;
}

.bg-wolf {
  background-color: var(--wolves-blue-dk) !important;
  padding: 8px;
}

.dp-conn-form {
  margin-left: auto;
  margin-right: 20px;
}

.dp-conn-btn {
  width: 200px;
  margin: 8px 0px 8px 0px;
}
