.App {
  display: flex;
  flex-direction: column;
  text-align: center;
  min-height: 100vh;
}

.no-margin {
  margin: 0px;
}

.single-line {
  line-height: 1em;
}

.font-14 {
  font-size: 14px;
}

.font-20 {
  font-size: 20px;
}

.font-24 {
  font-size: 24px;
}

.font-32 {
  font-size: 32px;
}

.ellipsis {
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.wolves-orange {
  color: var(--wolves-orange);
}

hr.wolves {
  border-top: var(--wolves-orange) 1px solid;
  margin: 4px 0px;
  align-self: stretch;
}

a:hover {
  text-decoration: none !important;
  border: 0 !important;
  color: var(--wolves-orange) !important;
}

.disabled-link {
  pointer-events: none;
}

.ticker-text {
  margin: 0 5px;
}
