.stake-main {
  background-image: url(../../assets/banner_stake.jpg);
  background-position: center;
  background-size: cover;
  padding: 0px 8% 30px 8%;
  color: var(--wolves-blue-dk);
  flex-grow: 1;
  display: flex;
  align-items: center;
}

.stake-container {
  margin: auto;
  width: 60%;
  min-width: 310px;
}

.stake-container h1 {
  font-family: 'vincente', serif;
  font-weight: bold;
  font-size: 52px;
  color: white;
}

.stake-control {
  position: relative;
  background-color: rgba(255, 255, 255, 0.7);
  margin-top: 40px;
  padding: 125px 5% 12px 5%;
  width: 100%;
}

.stake-opaque {
  opacity: 1;
}

.stake-logo {
  position: absolute;
  top: -25px;
  left: calc(50% - 100px);
}

.stake-line {
  display: block;
  height: 2px;
  width: 100%;
  margin-bottom: 80px;
  background-color: gray;
}

.stake-input-container {
  position: relative;
  font-size: 20px;
}

.stake-input {
  margin-right: 5px;
  height: 32px;
  width: 100%;
  padding: 0px 135px 0px 5px;
  vertical-align: bottom;
}

.stake-input-currency {
  vertical-align: middle;
  position: absolute;
  top: 2px;
  right: 4px;
}

.stake-input-max {
  position: absolute;
  cursor: pointer;
  font-size: 10px;
  border: black 1px solid;
  border-radius: 4px;
  vertical-align: inherit;
  padding: 1px;
  top: 8px;
  left: 4px;
}

.stake-top-margin {
  margin-top: 6px;
}

.stake-btn-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 3px -3px 0 -3px;
}

.stake-btn-grow {
  margin: 3px;
  flex-grow: 1;
}

.stake-links {
  width: 100%;
}

.stake-links a {
  color: var(--wolves-blue-dk);
}
